// cs-product-tile
// Single product tile for home/category/sliders 
//
// See http://warpspire.com/kss/ for documentation guidelines.
// Version 0.0.1
@import "../../cs-price/src/cs-price";
@import "../../cs-stars/src/cs-stars";
@import "../../cs-product-label/src/cs-product-label";
@import "../../cs-availability-info/src/cs-availability-info";
@import "../../cs-product-thumbnails/src/cs-product-thumbnails";

.cs-product-tile {
    width: 100%;
    position: relative;
    font-size: 1rem;
    box-sizing: border-box;

    $root: &;

    //temp
    padding-bottom: 300px;
    width: 300px;
    margin: 50px auto;
    font-family: "sans-serif";

    &__wrapper {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;

        background-color: #fff;
        padding: 1em;

        &:hover {
            margin-left: -1em;
            margin-right: -1em;
            margin-top: -1em;
            margin-bottom: -1em;
            padding: 2em;

            box-shadow: 0 0 4px 2px rgba(0, 0, 0, .5);

            & > #{$root}__photos > #{$root}__slider {
                display: block;
            }

            & > #{$root}__hidden-row {
                display: flex;
            }

        }
    }

    &__hidden-row {
        display: none;
        justify-content: space-between;
    }

    &__photos {
        height: calc(100% - 5em);
        position: relative;
    }
    &__image {
        max-width: 100%;
        max-height: 100%;
        margin: 0 auto;
        display: block;
    }
    &__slider {
        display: none;

        position: absolute;
        left: 0;
        top: 0;
        width: 2em;
        height: 4em;
        font-size: 0.9em;
    }
    &__label {
        position: absolute;
        top: 1em;
        right: 0;
        display: block;
        line-height: 2em;
        background-color: red;

    }
    &__price {
        height: 2em;
        font-size: 1.3em;

    }
    &__title {
        line-height: 2em;
    }
    &__reviews {
        height: 2em;
        font-size: 0.8em;

    }

    &__availability {
        font-size: 0.8em;

    }

}
